import * as React from "react";
import reactCSS from 'reactcss'
import { SketchPicker } from 'react-color'
import {ReactUtilities} from "shared/utilities/react/ReactUtilities";
import {ColorPopover} from "shared/controls/SingleInputs/SlateEditor/Buttons/ColorPopover";




export class ColorButton extends React.Component<ColorButtonProps, ColorButtonState> {
    static defaultProps: Partial<ColorButtonProps> = {
        Color:''
    };
    public Container:HTMLDivElement;
    constructor(props: ColorButtonProps) {
        super(props);
        this.state = {
            displayColorPicker:false,
            Top:0
        };
    }

    public render() {
        let colorToUse=this.props.Color!=''?this.props.Color:`#000`;
        const styles = reactCSS({
            'default': {
                color: {
                    width: '18px',
                    height: '14px',
                    borderRadius: '2px',
                    background: colorToUse
                },
                swatch: {
                    padding: '5px',
                    background: '#fff',
                    borderRadius: '1px',
                    boxShadow: '0 0 0 1px rgba(0,0,0,.1)',
                    display: 'block',
                    cursor: 'pointer',
                },
                popover: {
                    position: 'fixed',
                    zIndex: '2',
                    top:this.Container?this.Container.getBoundingClientRect().top+25:0
                },
                cover: {
                    position: 'fixed',
                    top: '0px',
                    right: '0px',
                    bottom: '0px',
                    left: '0px',
                },
            },
        });

        return (

            <div style={{display:'inline-block'}}>
                <div ref={x=>this.Container=x} style={ styles.swatch } onMouseDown={ (e)=>{e.preventDefault(); this.setState({displayColorPicker:!this.state.displayColorPicker})} }>
                    <div style={ styles.color } />
                    <ColorPopover ColorToUse={colorToUse} Container={this.Container} ColorChanged={x=>this.ColorSelected(x)} Visible={this.state.displayColorPicker} />
                </div>


            </div>
        )

    }

    private ColorSelected(color) {

        if(color!='') {
            ReactUtilities.ExecuteMethodPropIfDefined(this.props.ColorChanged,color);
            color='';
        }
        this.setState({displayColorPicker:false});
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface ColorButtonProps {
    ColorChanged:(color:string)=>void;
    Color:string;
}

interface ColorButtonState {
    displayColorPicker:boolean;
    Top:number;
}

//#endregion "My Region"