import * as React from "react";
import reactCSS from "reactcss";
import { SketchPicker } from 'react-color'
let ReactDOM = require("react-dom");

export class ColorPopover extends React.Component<ColorPopoverProps, ColorPopoverState> {
    static defaultProps: Partial<ColorPopoverProps> = {
        Container:null,
        Visible:false
    };

    public lastColor:any;
    constructor(props: ColorPopoverProps) {
        super(props);
        this.state = {};
    }

    public render() {

        const styles = reactCSS({
            'default': {
                color: {
                    width: '18px',
                    height: '14px',
                    borderRadius: '2px',
                    background: this.props.ColorToUse
                },
                swatch: {
                    padding: '5px',
                    background: '#fff',
                    borderRadius: '1px',
                    boxShadow: '0 0 0 1px rgba(0,0,0,.1)',
                    display: 'block',
                    cursor: 'pointer',
                },
                popover: {
                    position: 'absolute',
                    zIndex: '2',
                    top:this.props.Container?(this.props.Container.getBoundingClientRect().top+window.scrollY+this.props.Container.getBoundingClientRect().height+1)+'px':null,
                    left:this.props.Container?(this.props.Container.getBoundingClientRect().left+window.scrollX)+'px':null,
                    border:'1px #dfdfdf solid',
                    borderRadius:10
                },
                cover: {
                    position: 'fixed',
                    top: '0px',
                    right: '0px',
                    bottom: '0px',
                    left: '0px',
                },
            },
        });

        if(this.props.Visible)
            return ReactDOM.createPortal( (
                <div  style={ styles.popover as any } className={'rnColorPopOver rednao'}>
                    <div style={ styles.cover as any } onClick={ e=>{this.setState({ displayColorPicker: false })} }/>
                    <div onMouseDown={x=>x.stopPropagation()}>
                        <SketchPicker color={ this.props.ColorToUse} onChange={ e=>{this.lastColor=e.hex;}} />
                        <div style={{backgroundColor:'white',borderBottomRightRadius:10,borderBottomLeftRadius:10, textAlign:'center',marginBottom:2,position:'relative',zIndex:10}}>
                            <button className={'rnbtn rnbtn-primary'} onMouseDown={e=>this.props.ColorChanged&&this.props.ColorChanged(this.lastColor)} >Select</button>
                        </div>
                    </div>
                </div>
            ),document.body);
        return null;
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface ColorPopoverProps {
    Container:HTMLDivElement;
    Visible:boolean;
    ColorToUse:any;
    LastColor:any;
    ColorChanged:(color:any)=>void;
}

interface ColorPopoverState {

}

//#endregion "My Region"