import {FBFieldWithPriceBaseOptions, PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";


export class FBColorPickerFieldOptions extends FBFieldWithPriceBaseOptions{
    public DefaultColor:string;
    public Placeholder:string;

    public ShowQuantitySelector:boolean;




    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.Type=FieldTypeEnum.ColorPicker;
        this.Label='Color Picker';

        this.ShowQuantitySelector=false;
        this.QuantityPosition='bottom';
        this.QuantityMaximumValue=0;
        this.QuantityMinimumValue=0;
        this.QuantityDefaultValue='';
        this.QuantityPlaceholder='';
        this.QuantityLabel='Quantity';

        this.Placeholder='';
        this.DefaultColor='#ffffff';

        this.PriceType=PriceTypeEnum.none;


    }
}

